import { NgModule, ErrorHandler } from '@angular/core';
import { HttpModule } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { UserService } from '../providers/user-service/user-service';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/about/about.module.ngfactory#AboutModuleNgFactory', name: 'AboutPage', segment: 'about', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/contact/contact.module.ngfactory#ContactModuleNgFactory', name: 'ContactPage', segment: 'contact', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsModuleNgFactory', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user-detail/user-detail.module.ngfactory#UserDetailModuleNgFactory', name: 'UserDetailPage', segment: 'user-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/users/users.module.ngfactory#UsersModuleNgFactory', name: 'UsersPage', segment: 'users', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    UserService,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



